<template>

  <div class="widget-courselist">
    <div class="panel-title"></div>
    <div class="nodata" v-if="items.length===0"><img src="../../assets/image/me/nodata.png" alt=""></div>
    <ul class="pagelist-wrapper">

      <li class="impression-item impression-viewed" data-post-id="66420781" v-for="item in items" :key="item._id">


        <a  class="item-cover"  @click="blogDetail(item.blog._id)">
          <img :src="item.blog.cover">
        </a>
        <div class="item-info" style="width: 450px;">
          <a  class="item-title"  @click="blogDetail(item.blog._id)">{{item.blog.title}}</a>
          <p class="item-desc">{{item.blog.introduction}}</p>
        </div>


        <div class="item-related">
          <div class="item-tags has-tags">
            <a @click="blogDetail(item.blog._id)" v-for="tagitem in item.blog.tag"   class="event-tag popover-action" >{{tagitem.name}}</a>
          </div>
          <div class="item-handler">
            <span>
              <button class="cancel " @click="cancelBlog(item._id)"><i class="iconfont icon-star" ></i></button>

			      </span>

            <span>{{item.blog.author.name}}</span>

            <span> |

				25926


				 553


				 68

			</span>

          </div>
        </div>
      </li>

    </ul>
    <i class="icon-loading--yellow" style="display: none;"></i>
    <i class="icon-end" style="display: none;"></i>
  </div>
</template>

<script>
  export default {
    name: "CollectBlog" ,
    data(){
      return {
        items:[],
        id:{}
      }
    },
    methods:{
      blogDetail(id){


          this.$router.push(`/blogdetail/${id}`)

      }
      ,
      async cancelBlog(id){
        const res = await this.$http.delete(`rest/collect_blogs/${id}`)
        this.$router.push('collectblog')
        this.$message({
          type:'success',
          message:''
        })
        this.fetch()
      },

      async fetch(){
        const res = await this.$http.get(`rest/collect_blogs/${this.id}`)
        this.items = res.data
      },
    },
    created(){
      this.id =  localStorage.user
      this.fetch()
    }
  }
</script>

