<template>
  <div class="test3">
    <span class="namea"></span>
    <i class="el-icon-close" @click="" @click=hide()></i>
    <div class="msg" ref="box">
      <div v-for="item in list" :class="[item.type,'msg-item']">
        <img :src="item.avatar" alt="">
        <div class="p">
          {{item.content}}
        </div>
      </div>
    </div>
    <div class="input-group">
      <input type="text" v-model="model.contentText" @keyup.enter="sendText">
      <button @click="sendText"></button>
    </div>
  </div>
</template>

<script>
  export default {
    name: "index3",
    data() {
      return {
        list: [],//
        contentText: "",//input
        model: {
          user: '2',
          contentText: "",
          avatar: ''
        },
        message: '',
        first: true,
        user: {},
        userid: ''
      }
    },
    methods: {
      hide () {
        this.$emit('hide','')
      },
      //
      sendText() {
        let that = this;
        if(!this.first) {
          this.list = [...this.list, {type: "mine", content: this.model.contentText, avatar: this.user.avatar}];//typeminerobot
        }
        this.first = false
        this.backText(function () {
          that.model.contentText = "";//
        });
      },
      backText(callback) {
        let that = this;
        if (window.WebSocket) {
          let ws = new WebSocket("ws://localhost:8001");
          ws.onopen = function (e) {
            console.log("");
            console.log("that.contentText is", that.contentText);
            ws.send(JSON.stringify(that.model));
            callback();
          };
          ws.onclose = function (e) {
            console.log("")
          };
          ws.onerror = function () {
            console.log("")
          };
          ws.onmessage = function (e) {
            const obj = JSON.parse(e.data)
            console.log(that.message);
            console.log(obj.contentText);
            if(obj.user != '2' && obj.contentText != that.message) {
              that.message = obj.contentText
              console.log('');
              that.list = [...that.list, {type: "robot", content: obj.contentText, avatar: obj.avatar}]
            }}
        }
      },
      async fetchUser(){
        const res = await this.$http.get(`rest/user/${this.userid}`)
        this.user = res.data
        this.model.avatar = this.user.avatar
      },
    },
    watch: {
      //list,div
      list: function () {
        let that = this;
        setTimeout(() => {
          that.$refs.box.scrollTop = that.$refs.box.scrollHeight;
        }, 0);
        //setTimeoutvuedomdivscrollHeight
      }
    },
    mounted() {
      this.sendText()
    },
    created () {
      this.userid = localStorage.user
      this.fetchUser()
    }
  };


</script>

<style scoped lang="scss">

  .test3 {
    z-index: 11;
    background: #fff;
    text-align: center;
    position: absolute;
    border: 1px #b4b4b4 solid;
    width: 500px;
    height: 600px;
    border-radius: 10px;
    left: 50%;
    top: 10%;
    transform: translate(-50%, -50%);
    display: none;
    &.show {
      display: block;
    }
    .namea {
      position: absolute;
      font-size: 20px;
      font-weight: 600;
      padding-left: 10px;
      color: #6a6a6a;
    }
    .el-icon-close {
      cursor: pointer;
      position: absolute;
      right: 10px;
      top: 2px;
      font-size: 25px;
      &:hover {
        transform: rotate(45deg);
        transition: 0.1s ease;
      }
    }
  }
  .input-group {
    padding-top: 7px;
    input {
      margin-left: 15px;
      width: 300px;
      outline: none;
      border: none;
      background: #f2f2f2;
      height: 30px;
      font-size: 18px;
      border-radius: 5px;
    }
    button {
      cursor:pointer;
      margin-left: 20px;
      border-radius: 5px;
      width: 80px;
      height: 30px;
      border: none;
      &:hover {
        background: #09b4c5;
        color: #fff;
      }
      &:focus {
        outline: none;
      }
    }
  }
  .msg {
    width: 100%;
    height: 500px;
    overflow: auto;
    padding-top: 5px;
    background: #f4f5f7;
    margin-top: 30px;
    display: inline-block;
    margin-bottom: 6px;
    border-bottom: 1px #c0c0c0 solid;

    .msg-item {
      position: relative;
      overflow: hidden;
      margin-left: 10px;
      width: 95%;
      display: flex;
      margin-top: 18px;
      margin-bottom: 18px;
      img {
        margin: 0px;
        border-radius: 20px;
        width: 40px;
        height: 40px;
      }
      .p {
        margin-right: 5px;
        margin-left: 5px;
        max-width: 200px;
        border-radius: 15px;
        background: #23d5cb;
        color: white;
        padding: 6px 12px;
        text-align: left;
        box-sizing: border-box;
      }

      &.mine {
        flex-direction: row-reverse;
        .p {

          background: #4879ff;
          color: white;
        }
      }
    }
  }

</style>