<script>
import $ from 'jquery'
export default {
  data () {
    return {
      userid:{},
      user:{},
      model:{
        date:"",
        comment:[]
      },
      share:[],
      updatashare:{},
      updatetext:""
    }
  },
  methods:{
    getDate () {
      var nowDate = new Date();
      var year = nowDate.getFullYear();
      var month = nowDate.getMonth() + 1 < 10 ? "0" + (nowDate.getMonth() + 1)
          : nowDate.getMonth() + 1;
      var day = nowDate.getDate() < 10 ? "0" + nowDate.getDate() : nowDate
          .getDate();
      var dateStr = year + "-" + month + "-" + day;
      return dateStr
    },
    async updatecomment(item) {
      for(var i = 0; i < $('.updatecomment').length; i++){
        if($('.updatecomment').eq(i).val()){
          this.updatetext = $('.updatecomment').eq(i).val()
          $('.updatecomment').eq(i).val("")
        }
      }
      let nowDate = this.getDate()
      this.updateshare = item
      this.updateshare.comment.push({user:this.userid,text:this.updatetext,date:nowDate})
      let res
      res = await this.$http.put(`rest/share/${item._id}`,this.updateshare)
      this.fetchShare()

    },
    async fetchUser(){
      const res = await this.$http.get(`rest/user/${this.userid}`)
      this.user = res.data
    },
    async fetchShare(){
      const res = await this.$http.get('rest/share')
      this.share = res.data
    },
    afterUpload(res){
      this.$set(this.model,'img',res.url)
    },
    async save(){

      this.model.date = this.getDate()
      let res
      res = await this.$http.post('rest/share',this.model)
      this.$message({
        type:'success',
        message:''
      })
      this.model.img = ""
      this.model.text = ""
      this.fetchShare()
    },
  },
  created(){
    this.userid = localStorage.user
    this.fetchUser()
    this.fetchShare()
    this.model.user = localStorage.user
  }

};
</script>

